import React from "react";
import numeral from "numeral";
import moment from "moment";
import { Row, Column } from "components/Grid.tsx";

import sendgrid from "assets/img/logos/not-brex/color/sendgrid-light.png";
import zendesk from "assets/img/logos/not-brex/grayscale/light-text/zendesk.png";
import googleAds from "assets/img/logos/not-brex/grayscale/light-text/google-ads.png";
import wework from "assets/img/logos/not-brex/grayscale/light-text/wework.png";
import aws from "assets/img/logos/not-brex/grayscale/light-text/aws.png";

import {
  Container,
  DollarValue,
  SmallCaption,
  Title,
  BottomBodyWrapper,
  Body,
  MultiplierNumber,
  MultiplierContainer,
  Logo,
  PartnerWrapper,
  Primary,
  StyledLink,
  RewardsContainer,
  Subtitle,
} from "./elements";

const rewards = [
  {
    number: 7,
    title: "on rideshare",
    body:
      "Uber, Lyft, taxi, scooter. However you want to get there, you’ll earn points on every ride.",
  },
  {
    number: 4,
    title: "on brex travel",
    body: (
      <span>
        Book flights and hotel stays with Brex and{" "}
        <StyledLink
          href="https://brex.com/travel/"
          rel="noopener noreferrer"
          target="_blank"
        >
          <Primary>redeem rewards points for travel.</Primary>
        </StyledLink>
      </span>
    ),
  },
  {
    number: 3,
    title: "on restaurants",
    body:
      "Earn points back on dining and coffee shops. Great for team lunch or treating candidates.",
  },
  {
    number: 2,
    title: "on recurring software",
    body:
      "Turn your subscriptions into cash back—Salesforce, GitHub, Slack, anything you can put on a card.",
  },
  {
    number: 1,
    title: "on everything else",
    body:
      "Earn points for all your spend, including big ticket items like digital ads and servers. ",
  },
];

const partnerOffers = [
  { logo: googleAds, height: 29, width: 142 },
  { logo: zendesk, height: 26, width: 137 },
  { logo: sendgrid, height: 30, width: 139 },
  { logo: aws, height: 35, width: 59 },
  { logo: wework, height: 24.5, width: 114 },
];

class Rewards extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      beginningRewardsAmount: 105552027.49,
      rewardsAmount: 105552027.49,
      billboardDate: new Date(2019, 1, 5),
      monthlyIncrease: 250000,
    };
  }

  componentWillMount() {
    this.tick();
  }

  componentDidMount() {
    this.interval = setInterval(() => this.tick(), 750);
  }

  componentWillUnmount() {
    clearInterval(this.interval);
  }

  tick() {
    const milissecondsInAMonth = 30 * 24 * 60 * 60 * 1000;
    this.setState((prevState) => ({
      rewardsAmount:
        prevState.beginningRewardsAmount +
        (prevState.monthlyIncrease / milissecondsInAMonth) *
          moment().diff(prevState.billboardDate, "milliseconds"),
    }));
  }

  render() {
    const { rewardsAmount } = this.state;
    const { upmarket } = this.props;
    return (
      <Container>
        {/* Hiding the below div from Percy to reduce snapshot diff noise */}
        <DollarValue className="hidden-in-percy">
          {numeral(rewardsAmount).format("$0,0.00")}
        </DollarValue>
        <SmallCaption>
          {`BACK TO ${upmarket ? "COMPANIES" : "STARTUPS"} IN REWARDS`}
        </SmallCaption>
        <Title>Rewards for Brex Exclusive</Title>
        <Subtitle>
          Make Brex your exclusive corporate card and get in on the most
          valuable rewards program available with no cap on what you can earn.
        </Subtitle>
        <RewardsContainer>
          {rewards.map((reward) => (
            <MultiplierContainer key={reward.title}>
              <MultiplierNumber>{`${reward.number}x`}</MultiplierNumber>
              <SmallCaption textAlign="left">{reward.title}</SmallCaption>
              <Body>{reward.body}</Body>
            </MultiplierContainer>
          ))}
        </RewardsContainer>
        <Title>More cardholder perks</Title>
        <BottomBodyWrapper>
          <Body textAlign="center">
            Get discounts on the top services in the startup ecosystem.{" "}
            <StyledLink
              href="https://brex.com/rewards/"
              rel="noopener noreferrer"
              target="_blank"
            >
              <Primary>Learn more about partner offers.</Primary>
            </StyledLink>
          </Body>
        </BottomBodyWrapper>
        <Row>
          <Column span={1} />
          {partnerOffers.map((offer, i) => (
            <Column
              span={2}
              spanSmall={12}
              spanMedium={12}
              key={`partner-offer-${i}`}
            >
              <PartnerWrapper>
                <Logo
                  src={offer.logo}
                  height={offer.height}
                  width={offer.width}
                />
              </PartnerWrapper>
            </Column>
          ))}
          <Column span={1} />
        </Row>
      </Container>
    );
  }
}

export default Rewards;
