import React from "react";
import { Row, Column } from "components/Grid.tsx";

import {
  Title,
  Subtitle,
  TitleWrapper,
  Wrapper,
  DetailsSection,
  Section,
  Image,
  Number,
  SectionTitle,
  SectionDescription,
} from "./elements";

const AlternatingSequence = ({ messages }) => (
  <Wrapper>
    <TitleWrapper>
      <Title>{messages.title}</Title>
      <Subtitle>{messages.subtitle}</Subtitle>
    </TitleWrapper>
    <DetailsSection>
      {messages.sections.map((section, index) => (
        <Section key={section.title}>
          <Row
            reversedFlexDirection={index % 2 === 0}
            verticalAlignment="flex-start"
            horizontalAlignment="space-between"
          >
            <Column span={6} spanMedium={5} spanSmall={12}>
              <Image src={section.img} alt={section.title} />
              <Number>{`0${index + 1}`}</Number>
            </Column>
            <Column span={5} spanMedium={6} spanSmall={12}>
              <SectionTitle>{section.title}</SectionTitle>
              <SectionDescription>{section.description}</SectionDescription>
            </Column>
          </Row>
        </Section>
      ))}
    </DetailsSection>
  </Wrapper>
);

export default AlternatingSequence;
